/* Overlay style */
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(99,22,45,0.9);
  font-family:'Montserrat', sans-serif;;
}

/* Overlay closing cross */
.overlay .overlay-close {
  width: 35px;
  height: 35px;
  position: absolute;
  right: 40px;
  top: 20px;
  overflow: hidden;
  border: none;
  background: url(../img/cross.png) no-repeat center center;
  background-size: 100% 100%;
  text-indent: 200%;
  color: transparent;
  outline: none;
  z-index: 100;
}

.overlay .overlay-close:hover {
  cursor: pointer;
}

/* Menu style */
.overlay nav {
  text-align: center;
  position: relative;
  top: 50%;
  height: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  height: 100%;
  position: relative;
  z-index: 10;
  width: 50vw;
}

.project-detail .overlay nav, #projects .overlay nav  {
  height: 75%;
  transform: translateY(-50%);
}

.overlay ul li {
  display: block;
  height: calc(100% / 4);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  /*position: inherit;*/
  z-index: 10;
  font-size: 3.5vw;
  font-weight: 300;
}

.project-detail .overlay ul li, #projects .overlay ul li {
  display: block;
  height: calc(75% / 5);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  /*position: inherit;*/
  z-index: 10;
  font-size: 3.5vw;
  font-weight: 300;
}

.project-detail .overlay ul li:nth-of-type(1), .project-detail .overlay ul li:nth-of-type(2), .project-detail .overlay ul li:nth-of-type(8),
#projects .overlay ul li:nth-of-type(1), #projects .overlay ul li:nth-of-type(2), #projects .overlay ul li:nth-of-type(8) {
  display: block;
  height: calc(33.333% / 4);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  /*position: inherit;*/
  z-index: 10;
  font-size: 1.3vw;
}

.project-detail .overlay ul li:nth-of-type(2):after, #projects .overlay ul li:nth-of-type(2):after {
