  .header:after {
    content: 'Please rotate your smartphone';
    display: block;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0 ,0.75);
    font-size: 4vw;
    color: #fff;
    text-align: center;
    vertical-align: middle;
