/* --------------------------------

Primary style

-------------------------------- */
@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,700");
@import url("https://fonts.googleapis.com/css?family=Montserrat:100,200,300,600,700");
*, *::after, *::before {
  box-sizing: border-box;
  outline: 0; }

html {
  font-size: 62.5%;
  background-color: #fff; }

body {
  font-size: 1.6rem;
  color: #83b0b9;
  background-color: #fff; }

body#home {
  /*background: #fff url("../img/shapes.png") repeat center center;*/ }

body.noscroll {
  overflow: hidden; }

a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none; }

/* --------------------------------

 Main Content

-------------------------------- */
.cd-main {
  height: 100vh;
  overflow: hidden; }

.cd-section {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  padding-left: 0;
  /* Force Hardware Acceleration */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-transform: translateX(-100%);
  -moz-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0s 0.5s;
  -moz-transition: -moz-transform 0s 0.5s;
  transition: transform 0s 0.5s; }

.cd-section.overflow-hidden {
  /* this class is used to hide the scrolling bar while a new section is entering the viewport */
  overflow: hidden; }

.cd-section header {
  position: relative;
  height: 100vh;
  width: 100%;
  background: #83b0b9;
  overflow: hidden; }

.cd-section.visible {
  position: relative;
  z-index: 2;
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -ms-transform: translateX(0);
  -o-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition: -webkit-transform 0.5s 0s;
  -moz-transition: -moz-transform 0.5s 0s;
  transition: transform 0.5s 0s; }

@-webkit-keyframes cd-scroll-down {
  0% {
    -webkit-transform: translateX(-50%) scale(1); }
  50% {
    -webkit-transform: translateY(10px) translateX(-50%) scale(1); }
  100% {
    -webkit-transform: translateX(-50%) scale(1); } }

@-moz-keyframes cd-scroll-down {
  0% {
    -moz-transform: translateX(-50%) scale(1); }
  50% {
    -moz-transform: translateY(10px) translateX(-50%) scale(1); }
  100% {
    -moz-transform: translateX(-50%) scale(1); } }

@keyframes cd-scroll-down {
  0% {
    -webkit-transform: translateX(-50%) scale(1);
    -moz-transform: translateX(-50%) scale(1);
    -ms-transform: translateX(-50%) scale(1);
    -o-transform: translateX(-50%) scale(1);
    transform: translateX(-50%) scale(1); }
  50% {
    -webkit-transform: translateY(10px) translateX(-50%) scale(1);
    -moz-transform: translateY(10px) translateX(-50%) scale(1);
    -ms-transform: translateY(10px) translateX(-50%) scale(1);
    -o-transform: translateY(10px) translateX(-50%) scale(1);
    transform: translateY(10px) translateX(-50%) scale(1); }
  100% {
    -webkit-transform: translateX(-50%) scale(1);
    -moz-transform: translateX(-50%) scale(1);
    -ms-transform: translateX(-50%) scale(1);
    -o-transform: translateX(-50%) scale(1);
    transform: translateX(-50%) scale(1); } }

.cd-title {
  position: absolute;
  top: 50%;
  left: 0;
  /* this prevents title from jumping when the section scroll bar is visible */
  width: calc(100vw - 90px);
  padding: 1em;
  color: #ffffff;
  text-align: center;
  /* Force Hardware Acceleration */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
  opacity: 0;
  -webkit-transform: translateY(-50%) translateX(-80px);
  -moz-transform: translateY(-50%) translateX(-80px);
  -ms-transform: translateY(-50%) translateX(-80px);
  -o-transform: translateY(-50%) translateX(-80px);
  transform: translateY(-50%) translateX(-80px);
  -webkit-transition: -webkit-transform 0s 0.5s, opacity 0s 0.5s;
  -moz-transition: -moz-transform 0s 0.5s, opacity 0s 0.5s;
  transition: transform 0s 0.5s, opacity 0s 0.5s; }

.cd-title h2 {
  font-size: 2.8rem;
  font-weight: 700;
  margin-bottom: 20px; }

.cd-title span {
  opacity: 0.6; }

.visible .cd-title {
  opacity: 1;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  -webkit-transition: -webkit-transform 0.4s 0.3s, opacity 0.4s 0.3s;
  -moz-transition: -moz-transform 0.4s 0.3s, opacity 0.4s 0.3s;
  transition: transform 0.4s 0.3s, opacity 0.4s 0.3s; }

@media only screen and (min-width: 768px) {
  .cd-title h2 {
    font-size: 5rem; } }

.cd-content {
  padding: 2em 0;
  background-color: #fff;
  position: relative;
  /*display: none;*/
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0); }

.cd-content p {
  width: 80%;
  max-width: 800px;
  margin: 0 auto 2em;
  font-size: 1.4rem;
  line-height: 1.6;
  color: #999999; }

@media only screen and (min-width: 768px) {
  .cd-content {
    padding: 4em 0; }
  .cd-content p {
    font-size: 1.6rem; } }

